body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

@media only screen and (max-width: 900px) {
  body {
    display: block;
  }
}

.main-container
{
  display: flex;
  flex-direction: column;
}

.fade-in 
{
  display: flex;
  flex-direction: column;
  -webkit-animation: fadeIn 5s;
          animation: fadeIn 5s;
  justify-content: center;
}

@-webkit-keyframes fadeIn {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}

@keyframes fadeIn {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}

/*GENERAL BACKGROUND*/
body
{
  background: -webkit-linear-gradient(rgba(0,0,0,0.95), rgba(0,0,0,0.95)), no-repeat center;
  background: linear-gradient(rgba(0,0,0,0.95), rgba(0,0,0,0.95)), no-repeat center;
}

/* FONTS */
@font-face
{
  font-family: "Everson Mono";
  src: url(/static/media/EMLATIN6.49faf4a3.ttf);
}

@font-face
{
  font-family: "Cooper Hewitt";
  src: url(/static/media/CooperHewitt-Light.c673f1bd.otf);
}

@font-face
{
  font-family: "Cooper Hewitt Book";
  src: url(/static/media/CooperHewitt-Book.375f05aa.otf);
}

@font-face
{
  font-family: "DejaVu Sans Mono";
  src: url(/static/media/DejaVuSansMono.6a15e0a7.ttf);
}
.profile
{
  display: flex;
  flex-grow: 1;
  flex-direction: column;
  width: 100%;
  align-items: center;
  background-size: cover;
  background-position: center;
}

.profile-photo
{
  margin-top: 87px;
}

.profile-photo img
{
  max-width: 180px;
  max-height: 180px;
}

.profile-title h1
{
  text-align: center;
  margin-top: 20px;
  width: 100%;
  font-family: "DejaVu Sans Mono";
  color:rgba(255, 255, 255, 0.726);
  font-size: 40px;
  font-weight: medium;
  text-overflow: ellipsis;
}

#cursor {
  border-left: .45em solid rgb(202, 202, 202);
  -webkit-animation: blink 0.9s steps(1) infinite;
          animation: blink 0.9s steps(1) infinite;
}

@-webkit-keyframes blink {
  50% {
    border-color: transparent;
  }
}

@keyframes blink {
  50% {
    border-color: transparent;
  }
}

.main-nav
{
  display: flex;
  flex-direction: column;
  margin-top: 30px;
}

.main-nav ul
{
  padding: 0;
}

.nav-menu 
{
  list-style-type: none;
  display: flex;
  flex-direction: row;
  justify-content: center;
}

.home-nav-menu li a
{
  color: white;
  text-decoration: none;
  font-family: "Everson Mono";
  font-size: 24px;
  font-weight: bold;
  font-style: italic;
  margin: 5vw;
  overflow: hidden;
  -webkit-transition: 0.5s all ease;
  transition: 0.5s all ease;
}

.home-nav-menu li a:hover
{
  color: rgb(85, 85, 85);
}

.nav-media
{
  display: flex;
  flex-direction: row;
  justify-content: center;
  margin-top: 75px;
  list-style-type: none;
}

.nav-media li a
{
  margin: 25px;
  color: white;  
}

.nav-media li i:hover
{
  color: rgb(85, 85, 85);
  -webkit-transition: 0.5s all ease;
  transition: 0.5s all ease;
}

@media only screen and (min-height: 600px)
{
  .homepage
  {
    height: 100vh;
  }
}

@media only screen and (max-width: 400px)
{
  .profile-photo
  {
    margin-top: 10vh;
  }

  .profile-photo img
  {
    max-width: 37vw;
  }

  .profile-title h1
  {
    margin-top: 10px;
    font-size: 7vw;
    margin-bottom: 8vh;
  }

  .home-nav-menu li a
  {
    margin: 5vw;
    font-size: 5vw;
  }

  .nav-media li a
  {
    margin: 4vw;
  }

  .nav-media
  {
    margin-top: 23vh;
  }
}
.about-section
{
  margin-top: 24px;
  display: flex;
  flex-direction: column;
  align-items: center;
  margin-bottom: 24px;
}

.about-header
{
  display: flex;
  flex-direction: column;
  width: 100%;
  padding-top: 135px;
  padding-bottom: 135px;
  align-items: center;
  background: -webkit-linear-gradient(rgba(0,0,0,0.5), rgba(0,0,0,0.5)), url(/static/media/about1.fdd4234b.jpg) no-repeat;
  background: linear-gradient(rgba(0,0,0,0.5), rgba(0,0,0,0.5)), url(/static/media/about1.fdd4234b.jpg) no-repeat;
  background-size: cover;
  background-position: center;
}

.about-header h1
{
  color: rgb(252, 142, 16);
  text-align: center;
  font-family: "Everson Mono";
  font-size: 35px;
  font-style: italic;
  font-weight: bold;
}

.description
{
  color: white;
  width: 70%;
  font-family: "Cooper Hewitt";
}

.description p
{
  font-weight: medium;
  margin-top: 60px;
  font-size: 19px;
  text-align: justify;
  line-height: 1.4em;
}

.description a
{
  color: white;
  font-weight: medium;
  font-style: italic;
  font-family: "Cooper Hewitt";
  -webkit-transition: 0.5s all ease;
  transition: 0.5s all ease;
}

.description a:hover
{
  color: rgb(85, 85, 85);
}

.cat-highlight
{
  color:rgb(252, 142, 16);
}

@media only screen and (max-width: 700px)
{
  .about-header
  {
    padding-top: 3vh;
    padding-bottom: 3vh;
  }

  .description
  {
    width: 80%;
  }

  .description p
  {
    font-size: 4vw;
  }
}

@media only screen and (min-width: 1400px)
{
  .about-header
  {
    background-size: contain;
    background-repeat: repeat-x;
  }
}
.photos-header h1
{
    color: white;
    text-align: center;
    font-family: "Everson Mono";
    font-style: italic;
    font-size: 32px;
    font-weight: medium;
}

.photos img
{
  max-width: 100%;
  height: auto;
}

.photos
{
  display: flex;
  flex-wrap: wrap;
  margin: 0px 150px;
}

.column 
{
  flex: 25% 1;
  max-width: 30%;
  margin-left: auto;
  margin-right: auto;
}
 
@media screen and (max-width: 900px) {
  .photos {
    margin: 10px 10px;
  }
}

.box
{
  position: relative; 
  margin-top: 10px;
}

.box:hover img
{
  opacity: 0.3;
  -webkit-transition: 0.5s all ease;
  transition: 0.5s all ease;
}

.box:hover .detail
{
  opacity: 0;
}

.column img {
  vertical-align: middle;
}

.photos-link
{
  margin-top: 78px;
  font-family: "Everson Mono";
  font-size: 15px;
  font-weight: bold;
  color: white;
  text-align: center;
}

@media only screen and (min-height: 1000px)
{
  .photos {
    margin: 10px 10px;
  }
}
.projects-header h1
{
    color: white;
    text-align: center;
    font-family: "Cooper Hewitt";
    font-size: 32px;
    font-weight: bold;
    font-style: italic;
}

.projects-section
{
    margin-bottom: 24px;
}

.projects-section ul
{
    padding: 0;
}

.project
{
    margin-top: 4px;
    display: flex;
    flex-grow: 1;
    flex-direction: column;
    width: 100%;
    align-items: center;
    background: rgb(39, 39, 39);
    padding-top: 30px;
    padding-bottom: 30px;
}

.project-photo img
{
    max-height: 165px;
    max-width:  165px;
}

.project-photo2 img
{
    height: 165px;
    width:  165px;
}

.projects-list h2
{
    font-family: "Everson Mono";
    font-size: 33px;
    color: rgb(252, 142, 16);
    font-weight: bold;
    text-align: center;
    margin: 8px;
}

.projects-list h3
{
    color: rgb(255, 255, 255);
    margin-top: 0;
    font-weight: bold;
    font-size: 17px;
    text-align: center;
    text-decoration: underline;
    font-style: oblique;
    font-family: "Everson Mono";
}

.skills
{
    display: flex;
    font-family: "Everson Mono";
    flex-direction: row;
    justify-content: center;
}

.skills li
{
    color: rgb(170, 170, 170);
    font-size: 14px;
    font-weight: bold;
    text-decoration: none;
    border: 2px solid rgb(170, 170, 170);
    border-radius: 9px;
    padding: 5px 16px;
    margin: 5px;
    display: flex;
}

.project-description
{
    margin-top: 25px;
    color: white;
    width: 40%;
    font-family: "Cooper Hewitt";
}

.project-description p
{
    font-weight: medium;
    font-size: 18px;
    text-align: justify;
    line-height: 1.2em;
    margin-bottom: 6px;
}

.github-link
{
    margin-top: 10px;
}

.github-link a
{
    color: white;
    -webkit-transition: 0.5s all ease;
    transition: 0.5s all ease;
}

.github-link a:hover
{
    color: rgb(85, 85, 85);
}

.demo-link
{
    margin-top: 15px;
}

.demo-link a
{
    color: white;
    -webkit-transition: 0.5s all ease;
    transition: 0.5s all ease;
}

.demo-link a:hover
{
    color: rgb(85, 85, 85);
}

@media only screen and (max-width: 700px)
{
    .project-description
    {
        width: 80%;
    }

    .project-description p
    {
        font-size: 4vw;
    }
}

@media only screen and (max-width: 400px)
{
    .skills
    {
        display: flex;
        flex-direction: column;
        align-items: center;
    }

    .skills li
    {
        color: rgb(170, 170, 170);
        font-size: 4vw;
        font-weight: bold;
        text-decoration: none;
        border: 1px solid rgb(170, 170, 170);
        border-radius: 9px;
        padding: 2px 3px;
        margin: 3px;
    }
}
.footer-section
{
  margin-top: 275px;
  background: rgb(39, 39, 39);
  display: flex;
  flex-direction: column;
}

.email
{
  margin-top: 20px;
  text-align: center;
}

.email a
{
  color: white;
  font-family: "Everson Mono";
  font-size: 14px;
  font-weight: bold;
}

.footer p
{
  margin-top: 15px;
  color: white;
  text-align: center;
  font-family: "Everson Mono";
  font-size: 12px;
  font-weight: bold;
  font-style: italic;
}
